/* 基本設定はそのまま */
.word-typing-container {
  text-align: center;
  color: white;
  margin-top: 20px;
}

.word-typing-japanese {
  font-size: 48px;
  margin: 20px 0;
}

.word-typing-english {
  font-size: 40px;
  margin: 10px 0;
  color: lightgray;
}

.word-typing-input-container {
  display: flex;
  position: relative;
  margin: 20px auto;
  width: 500px;
}

.word-typing-input {
  padding: 20px;
  font-size: 40px;
  width: 100%;
  text-align: center;
  color: white;
  background-color: #1e1e2f;
  border: 2px solid gray;
  border-radius: 10px;
  outline: none;
  transition: border-color 0.2s ease;
}

.word-typing-input.correct {
  color: lightgreen;
  border-color: lightgreen;
}

.word-typing-enter-key {
  display: block;
  position: absolute;
  top: 50%;
  left: 510px;
  transform: translateY(-40%);
  width: 70px;
  height: 70px;
  cursor: pointer;
  transition: transform 0.2s ease;
}

.word-typing-enter-key:hover {
  transform: translateY(-50%) scale(1.1);
}

/* スマホ版用の調整 */
@media screen and (max-width: 768px) {
  .word-typing-japanese {
    font-size: 32px; /* フォントサイズを縮小 */
  }

  .word-typing-english {
    font-size: 25px; /* 英語フォントも調整 */
  }

  .word-typing-input-container {
    width: 100%; /* コンテナ幅をデバイス幅に合わせる */
  }

  .word-typing-input {
    font-size: 25px; /* 入力欄のフォントサイズ縮小 */
    padding: 10px; /* パディングを調整 */
  }

  .word-typing-enter-key {
    left: auto; /* 左固定を解除 */
    right: 5px; /* スマホ版では右端に配置 */
    top: 50%; /* 縦方向は中央 */
    width: 50px; /* サイズを小さく調整 */
    height: 50px;
  }
}
