.lobby-container {
    background-size: cover; /* 画像をカバーする */
    display: flex;
    flex-wrap: wrap;
    justify-content: center; /* 中央揃え */
    padding: 50px;
    max-width: 2300px;
    margin: 0 auto;
  }
  
  .stage-card {
    width: 400px;
    height: 200px;
    background: #1e1e2f;
    border: 2px solid lightgreen;
    border-radius: 10px;
    margin: 30px;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    transition: transform 0.2s ease;
    text-decoration: none;
  }
  
  .stage-card:hover {
    transform: scale(1.05); /* ホバー時に拡大 */
  }
  
  .stage-title {
    font-weight: bold;
    font-size: 40px;
    color: lightgreen;
    margin-bottom: 10px;
  }
  
  .stage-mode {
    font-size: 30px;
    color: #ffffff;
    margin-bottom: 5px;
  }
  
  .stage-stars {
    display: flex;
    justify-content: center;
  }
  
  .star {
    color: #ffd700; /* 星の色 */
    font-size: 30px;
    margin: 0 2px;
  }
  