.app-container {
  text-align: center;
}

.main-content {
  display: flex;
  flex-direction: column;
  align-items: center; /* 横方向の中央揃え */
  justify-content: center; /* 縦方向の中央揃え */
  background-color: #282c34; /* 背景色を適用 */
  margin: 0; /* 余白をリセット */
}

/* ステージヘッダーのスタイル */
.stage-header {
  font-size: 24px;
  color: white;
  margin-bottom: 20px; /* 下の隙間を設定 */
}

/* ステータスバー全体のコンテナ */
.status-bar-container {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 20px; /* 次の要素との間隔 */
}

/* ステータスバーの背景 */
.status-bar {
  width: 50%; /* ステータスバーの横幅 */
  height: 15px;
  background-color: #c6c6c6; /* グレーの背景色 */
  border-radius: 5px;
  overflow: hidden;
  position: relative;
}

/* ステータスバーの進捗部分 */
.status-bar-progress {
  width: 0%; /* 初期状態の幅（進捗によって変更する） */
  height: 100%;
  background-color: hsl(189, 78%, 60%); /* 進捗の色 */
  transition: width 0.3s ease-in-out; /* 幅が変わる際のアニメーション */
}

/* ステータスバー横のテキスト */
.status-text {
  margin-left: 10px;
  color: white;
  font-size: 20px;
}

/* スマホ版のスタイル */
@media screen and (max-width: 768px) {
  .main-content {
    padding-top: 10px; /* スマホ向けの余白調整 */
  }

  h2 {
    font-size: 24px; /* 見出しサイズを縮小 */
    margin-bottom: 10px; /* 見出しとサブテキストの間隔調整 */
  }

  p {
    font-size: 18px; /* サブテキストサイズを縮小 */
    margin-bottom: 20px;
  }

  input {
    font-size: 16px; /* 入力欄の文字サイズを縮小 */
    padding: 8px; /* パディングを縮小 */
    width: 90%; /* 入力欄の幅を調整 */
  }

  .word-typing-enter-key {
    width: 40px; /* エンターキー画像の幅縮小 */
    height: 40px;
    margin-top: 10px; /* 上下の間隔を調整 */
  }
}
